body {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body * {
  box-sizing: inherit; }

svg.icon {
  fill: currentColor;
  width: 1em;
  height: 1em; }

a:not([class]) {
  text-decoration: underline;
  color: #D72027; }

.row {
  margin-right: auto;
  margin-left: auto;
  width: 100%;
  max-width: 1200px; }

@media (min-width: 42em) {
  .grid {
    display: flex; } }

.column {
  display: flex;
  flex: 1; }

/* Navbar container  */
.navbar {
  display: table;
  overflow: hidden;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  background-color: rgba(10, 10, 10, 0.8);
  font-family: Arial; }

.navbar > li {
  display: table-cell; }

.navbar ul {
  list-style-type: none;
  padding-left: 0; }

/* Links inside the navbar */
.navbar a {
  display: inline-block;
  font-size: 16px;
  color: #fafafa;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none; }

/* The dropdown container */
.dropdown {
  overflow: hidden; }

/* Dropdown button */
.dropdown .dropbtn {
  position: relative;
  font-size: 16px;
  border: none;
  outline: none;
  padding: 14px 16px;
  /* Important for vertical align on mobile phones */
  margin: 0;
  /* Important for vertical align on mobile phones */ }

.dropdown .dropbtn::after {
  content: '';
  display: inline-block;
  position: absolute;
  right: 2px;
  top: 50%;
  transform: translateY(-50%);
  border-top: 5px solid #D72027;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent; }

.dropdown:hover .dropbtn::after {
  border-top-color: #fafafa; }

/* Add a red background color to navbar links on hover */
.navbar a:hover,
.menu-item:hover {
  background-color: #D72027; }

/* Dropdown content (hidden by default) */
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(10, 10, 10, 0.2);
  z-index: 1; }

/* Links inside the dropdown */
.dropdown-content a {
  float: none;
  color: #0a0a0a;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left; }

/* Add a grey background color to dropdown links on hover */
.dropdown-content a:hover {
  background-color: #ddd; }

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block; }

.card {
  display: flex;
  margin: 0 1rem 1rem;
  padding: 1rem;
  height: auto;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #F0F0F0;
  color: #0a0a0a;
  text-decoration: underline transparent; }

.card:hover .card__heading,
.card:hover .card__button {
  text-decoration: underline currentColor; }

.card:hover .card__icon {
  top: -0.25rem;
  transform: rotate(3deg); }

.card-icons {
  margin-top: 2.5rem;
  padding-top: 0; }

@media (min-width: 42em) {
  .card-icons {
    margin-bottom: 4rem; } }

.card__icon {
  position: relative;
  top: 0;
  margin: -2.5rem auto 0;
  padding: 1rem;
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  background-color: #D72027;
  color: #fafafa;
  fill: currentColor;
  transition: top 0.2s, transform 0.2s; }

.card__heading {
  margin-bottom: 1rem;
  text-align: center;
  text-decoration: underline transparent;
  transition: text-decoration 0.2s; }

.card__content {
  margin: 0;
  padding-bottom: 2rem; }

.card__button {
  margin-top: auto;
  padding: 1rem;
  background-color: #0a0a0a;
  color: #fafafa;
  text-align: center;
  text-decoration: underline transparent;
  transition: text-decoration 0.2s, background-color 0.2s; }

.card__button:hover {
  background-color: #D72027; }
